const $siteList = $('.siteList') // å£°æ˜$siteListå°†ä¸ºè¦æ’çš„èŠ‚ç‚¹
const $lastLi = $siteList.find('li.last')// é€šè¿‡æ•°ç»„findå‡½æ•°æ‰¾åˆ°liçš„lastå±æ€§å¹¶å£°æ˜$lastLi
const $iconDelete = $('use.icon-delete')
const x = localStorage.getItem('x')
const xObject = JSON.parse(x)// è§£æJSONå­—ç¬¦ä¸²ï¼Œæ„é€ æ•°ç»„
// ç”¨å“ˆå¸Œè¡¨å­˜å‚¨ç½‘ç«™
const hashMap = xObject || [
    { logo: 'C', url:'https://blog.csdn.net/JankoY?spm=1011.2124.3001.5343'},
    { logo: 'I', url:'https://www.iconfont.cn/'},
    { logo: 'F', url:'https://figma.com/'}
]
const simplifyUrl = (url) => {
    return url.replace('https://', '')
    .replace('http://', '')
    .replace('www.', '')
    .replace(/\/.*/, '') // åˆ é™¤é“¾æ¥/åé¢çš„å†…å®¹
}

const render = () => { // é€šè¿‡è°ƒç”¨å‡½æ•°çš„æ–¹å¼ï¼Œè°ƒç”¨æ•°æ®ç»“æ„å¢åˆ åˆ—è¡¨
$siteList.find('li:not(.last)').remove()
hashMap.forEach((node,index)=>{// éå†èŠ‚ç‚¹å’Œä¸‹æ ‡
    const $li = $(`<li>
        <div class="site">
            <div class="logo">${node.logo[0]}</div>
            <div class="link">${simplifyUrl(node.url)}</div>
            <div class="close">
            <svg class="icon">
            <use class="icon-delete" xlink:href="#icon-shanchu"></use>
            </svg></div>
        </div>
        </a>
    </li>`).insertBefore($lastLi)
    $li.on('click', ()=>{
        window.open(node.url)
    })
    $li.on('click', '.close', (e)=>{
        e.stopPropagation()
        hashMap.splice(index, 1)
    render()
    })
})
}

render()

$('.addButton').on('click', () =>{
    let url = window.prompt('è¯·è¾“å…¥å°†è¦æ·»åŠ çš„ç½‘å€ï¼š')
    if(url.indexOf('http')!==0) {
        url = 'https://' + url
    }
    hashMap.push({//pushï¼šå¾€æ•°ç»„æœ€åæ’å…¥æ–°ä¸‹æ ‡
        logo: simplifyUrl(url)[0].toUpperCase(), 
        url: url})
render()
})

$('.addButton').hover(function(){
      $( $iconDelete ).hide;
    }, function(){
      $( $iconDelete ).show;
    }
)

